<template>
  <CRow class="justify-content-center">
    <CCol>
      <CCard>
        <CCardHeader class="d-flex justify-content-between">
          <div>
            <slot name="header">
              <CIcon name="cil-healing" />
              <strong>Sales</strong>
            </slot>
          </div>
        </CCardHeader>
        <CCardBody>
          <v-client-table :data="rows" :columns="columns" :options="options">
            <div slot="view_list" slot-scope="{ row }">
              <CButton
                class="mr-3"
                variant="outline"
                @click="openModal(row)"
                color="primary"
                v-c-tooltip="'View Bill'"
              >
                <CIcon name="cil-arrow-circle-right" />
              </CButton>
              <CButton
                class="mr-3"
                variant="outline"
                @click="downloadPDF(row)"
                color="success"
                v-c-tooltip="'Download PDF'"
              >
                <CIcon name="cil-check-circle" />
              </CButton>
            </div>
          </v-client-table>
        </CCardBody>
      </CCard>
    </CCol>
    <sweet-modal
      :blocking="Boolean(true)"
      ref="modal"
      overlay-theme="dark"
    >
      <div id="element-to-print">
        <CSidebarBrand class="d-md-down-none">
          <CIcon 
            class="c-sidebar-brand-full" 
            name="logo" 
            size="custom-size" 
            :height="45"
            :width="150"
            viewBox="30 220 550 200"
            style="margin-bottom:8px"
          />
        </CSidebarBrand>
        <CRow alignHorizontal="between">
          <p style="margin-left:15px;margin-bottom:-3px">{{ name }}</p>
          <p style="margin-right:15px;margin-bottom:-3px">{{ date }}</p>
        </CRow>
        <CDataTable :items="items" :fields="fields" hover> </CDataTable>
        <hr style="margin:0"/>
        <CRow alignHorizontal="between" style="margin-bottom:8px">
          <p style="margin-left:40px;margin-bottom:-3px">Total</p>
          <p style="margin-right:48px;margin-bottom:-3px;font-weight:bold">Rs: {{ total }}</p>
        </CRow>
        <hr style="margin:0"/>
        <p style="text-align:center">Thank you come again</p>
      </div>
    </sweet-modal>
  </CRow>
</template>
<script>
import moment from "moment";
import html2pdf from "html2pdf.js";
export default {
  name: "Sales",
  data() {
    return {
      total: 0,
      date: "",
      name: "",
      rows: [],
      items: [],
      fields: ["name", "selling_price", "qty", "price"],
      columns: [
        "invoice_number",
        "date",
        "customer",
        "name",
        "total",
        "view_list",
      ],
      options: {
        columnsClasses: {
          view_list: "text-center",
          date: "text-center",
        },
        filterByColumn: true,
        perPage: 10,
        texts: {
          filter: "Filter:",
          filterBy: "Filter by {column}",
          count: " ",
        },
        filterable: ["date", "invoice_number", "customer"],
        headings: {
          name: "Cashier",
        },
      },
    };
  },
  mounted() {
    this.getData();
  },
  computed: {
    isAdmin: function () {
      return this.$store.getters.isAdmin;
    },
  },
  methods: {
    getData() {
      if (this.isAdmin) {
        const user = localStorage.getItem('username')
        this.$http
          .get("http://localhost:3000/sales/get",{user})
          .then((res) => {
            this.rows = res.data;
            res.data.forEach((item) => {
              let local = moment.utc(item.date).local().format("YYYY-MM-DD");
              item.date = local;
              if (item.customer) {
                item.customer =
                  item.customer.firstname + " " + item.customer.lastname;
              } else {
                item.customer = "In-store customer"
              }
            });
          })
          .catch((err) => {
            // eslint-disable-next-line no-console
            if (err.response.status == 401) {
              this.$store.dispatch("logout").then(() => {});
            }
          });
      } else {
        this.$http
          .get(
            "http://localhost:3000/sales/getByUser/" +
              sessionStorage.getItem("user")
          )
          .then((res) => {
            this.rows = res.data;
            res.data.forEach((item) => {
              let local = moment.utc(item.date).local().format("YYYY-MM-DD");
              item.date = local;
              if (item.customer) {
                item.customer =
                  item.customer.firstname + " " + item.customer.lastname;
              }
            });
          })
          .catch((err) => {
            // eslint-disable-next-line no-console
            if (err.response.status == 401) {
              this.$store.dispatch("logout").then(() => {});
            }
          });
      }
    },
    openModal(row) {
      console.log(row);
      this.total = row.total;
      this.date = row.date;
      this.name = row.name;
      this.items = [];
      this.items = row.items;
      this.$refs.modal.open("modal");
    },
    downloadPDF(row) {
      this.total = row.total;
      this.date = row.date;
      this.name = row.name;
      this.items = [];
      this.items = row.items;
      html2pdf().set({
        pagebreak: { mode: ["avoid-all", "css", "legacy"] },
      });
      var element = document.getElementById("element-to-print");
      var opt = {
        margin: 0.1,
        filename: row._id,
        image: { type: "jpeg", quality: 0.98 },
        html2canvas: { scale: 2 },
        jsPDF: { unit: "in", format: "a6", orientation: "portrait" },
      };
      html2pdf(element, opt);
    },
    click(e) {
      console.log(e);
    },
    goToReceipt(row) {
      this.$router.push({ name: "Receipt", params: { row: row } });
    },
  },
};
</script>